.main {
  padding-top: 80px;
  padding-left: 0;
  padding-right: 0;
  padding-bottom: 0;
  border-top: solid 1px rgba(0,0,0,.3);
  margin: 0 70px;

  list-style-type: none;

  display: grid;
  grid-template-columns: repeat(auto-fit, 364px);
  grid-template-rows: auto;
  justify-content: center;
  gap: 24px;
  row-gap: 20px;
}

.container {
  display: flex;
  flex-direction: column;
  padding-bottom: 182px;
}

