@media screen and (max-width: 1279px) {

  .movies-list {
    grid-template-columns: repeat(auto-fit, 300px);
    gap: 30px;
    margin: 0 30px;
  }
  
}

@media screen and (max-width: 420px) {

  .movies-list {
    grid-template-columns: repeat(auto-fit, 300px);
    gap: 8px;
    padding: 50px 10px 0 10px;

    margin: 0 14px;
  }

  .button {
    margin-top: 70px;
    width: 240px;
    height: 36px;
  }

}
