.App {
  background: url('../src/image/back.jpg');
  -webkit-animation: Animation 20s ease infinite;
  -moz-animation: Animation 20s ease infinite;
  animation: Animation 20s ease infinite;
}

@-webkit-keyframes Animation {
  from{background-position:10% 0%}
  20%{background-position:20% 30%}
  30%{background-position:30% 40%}
  40%{background-position:30% 50%}
  50%{background-position:25% 70%}
  60%{background-position:35% 80%}
  70%{background-position:50% 70%}
  80%{background-position:40% 50%}
  90%{background-position:20% 20%}
  to{background-position:10% 0%}
}
@-moz-keyframes Animation {
  from{background-position:10% 0%}
  20%{background-position:20% 30%}
  30%{background-position:30% 40%}
  40%{background-position:30% 50%}
  50%{background-position:25% 70%}
  60%{background-position:35% 80%}
  70%{background-position:50% 70%}
  80%{background-position:40% 50%}
  90%{background-position:20% 20%}
  to{background-position:10% 0%}
}
@keyframes Animation { 
  from{background-position:10% 0%}
  20%{background-position:20% 30%}
  30%{background-position:30% 40%}
  40%{background-position:30% 50%}
  50%{background-position:25% 70%}
  60%{background-position:35% 80%}
  70%{background-position:50% 70%}
  80%{background-position:40% 50%}
  90%{background-position:20% 20%}
  to{background-position:10% 0%}
}
